import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import * as app_globals from '../../app/app-globles';

/**
 * Generated class for the InformationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-information',
  templateUrl: 'information.html',
})
export class InformationPage {
  public slideData = [{ image: "../../assets/imgs/slider/1.jpg" },{ image: "../../assets/imgs/slider/2.jpg" },{ image: "../../assets/imgs/slider/3.jpg" },{ image: "../../assets/imgs/slider/4.jpg" },{ image: "../../assets/imgs/slider/5.jpg" },{ image: "../../assets/imgs/slider/6.jpg" },{ image: "../../assets/imgs/slider/7.jpg" },{ image: "../../assets/imgs/slider/8.jpg" },{ image: "../../assets/imgs/slider/9.jpg" },{ image: "../../assets/imgs/slider/10.jpg" },{ image: "../../assets/imgs/slider/11.jpg" }]
public contentURL: any;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.contentURL=app_globals.k_content_url;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad InformationPage');
  }

}



